.home_container{
    padding: 5%;
    display: flex;
    flex-wrap: wrap;
    height: 80vh;
    width: 100%;
    margin-bottom: 20px;
}

.home_img{
    width: 45%;
    padding: 10px;
}
.home_img img{
    width: 400px;
    height: 400px;
}
.home_text{
    padding: 0px;
    justify-content: center;
    display: flex;
    flex-direction: column;
}
.home_text p{
    line-height: 30px;
    margin-bottom: 30px;
}
@media (max-width: 450px){
    .home_img{
        width: 100px;
        padding-left: 30px;
        padding: 10px;
    }
    .home_img img{
        width: 200px;
        height: 200px;
    }
    .home_text{
        padding: 10px;
        justify-content: center;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .home_text h3{
        font-size: 18px;
        text-align: center;
        color: rgb(39, 86, 102);
    }
    .home_text p{
        line-height: 1;
        margin-bottom: 10px;
        line-height: 2;
        font-size: 12px;
        text-align: center;
    }
}
.home_text a{
    text-decoration: none;
    color: black;
}
.home_btn{
        border: none;
        outline: none;
        background: rgb(39, 86, 102);
        color: white;
        width: 50%;
        height: 40px;
        display: block;
        cursor: pointer;
        font-size: 11px;
        text-transform: uppercase;
        letter-spacing: 2px;
        margin: 15px 0;
}
.card button:hover{
    background: rgb(39, 86, 102);
}
